.ContentRatings {
    margin: 0 10vw;
    color: whitesmoke;
}

#introParagraph {
    font-size: 150%;
}

.ratingLogo {
    align-items: center;
    width: 100%;
    height: auto;
}

.ratingSummary {
    font-size: 1.25rem;
}

#contentRatingGuidelinesButton {
    font-size: 2rem;
    margin: 2vh;

    display: flex;
    justify-content: center;
}

#demo-video {
    width: 48vw;
    height: 31vw;
}


@media screen and (max-width: 768px) {
    .ratingSummary {
        font-size: 1rem;
    }

    .ContentRatings h3 {
        margin-top: 1vh;
    }
    
    .ratingLogo {
        width: 120px;
        height: 90px;
    }

    #demo-video {
        width: 96vw;
        height: 58vw;
    }
}

@media screen and (max-width: 992px) {
    #demo-video {
        width: 64vw;
        height: 40vw;
    }
}