.ArtItem .figure-img {
    object-fit: cover;
    width: 20vw;
    height: 20vw;
}

.artModal {
    display: flex;
    justify-content: center;
}

.artModal .modal-content {
    width: unset;
}

@media screen and (max-width: 1024px) {
    .ArtItem .figure-img {
        width: 30vw;
        height: 30vw;
    }
}

@media screen and (max-width: 767px) {
    .ArtItem .figure-img {
        width: 40vw;
        height: 40vw;
    }
}