.App {
  position: relative;
  min-height: 100vh;
  padding: 9vh 0 12vh 0;

  font-family: 'Comfortaa', sans-serif;

  text-shadow: grey 1px 1px;
}

.backgroundImage {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.Title {
  font-family: 'Pasti';
  font-size: 5rem;
  text-align: center;
  font-style: italic;
  letter-spacing: 0.5rem;
  text-shadow: #e871af 4px 4px;
  margin: 2vh 0;
}